<template>
  <div id="app">
    <p class="animated lightSpeedIn">YD翻译</p>
    <h3 class=" animated rollIn">翻你所想</h3>
    <div class="content row">
    <translateform v-on:formSubmit="translateText" class="col-md-6 col-md-offset-1" />
    <!--v-bind:translatedText="translatedText"绑定要传递的数据-->
    <!--v-bind用于绑定属性和数据-->
    <translateOutput :translatedText="translatedText" class="col-md-5"/>
    </div>
  </div>
</template>

<script>

import Translateform from './components/Translateform'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'App',
  data(){
  	return{
  		translatedText:""
  	}
  },
  components: {
    'translateform':Translateform,
    'translateOutput':TranslateOutput
  },
  methods:{
  	//接受传递过来的数据
  	translateText:function(text,language){
  		//请求数据
 		this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180129T072723Z.cb3a923c76284e2d.542ce60d9ed21a8baac9176a8e9645df44b45713&lang='+language+'&text='+text)
          .then((response)=>{
            //得到翻译后的内容
            this.translatedText = response.body.text[0];
          })
  	}
  }
}
</script>

<style>

  html,body{
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: url("http://promo.prestigio.com/grace1/img/gloves-bg.jpg") no-repeat;
    background-size: 100% 100%;
  }

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

  .lightSpeedIn{
    animation-delay: 500ms;
  }

  .rollIn{
    animation-delay: 1000ms;
  }

  p{
    font-size: 80px;
    color: white;
    font-family: 华文行楷;
  }

  h3{
    font-size: 50px;
    color: white;
    font-family: 楷体;
  }

</style>



