<template>
  <div class="translateOutput">
    <textarea class="textarea animated rotateInUpRight"  cols="60" rows="10"> {{translatedText}}</textarea>
    <!--<h2>{{translatedText}}</h2>-->
  </div>
</template>

<script>


export default {
  name: 'translateOutput',
  //接受传递过来的数据
  props:[
     "translatedText"
  ]
}
</script>

<style >
	.translateOutput{
	  margin: 120px auto;
	}

  .textarea{
    border-radius: 15px;
    margin-left: -40%;
    max-width: 463px;
    max-height: 262px;
    padding: 10px;
    font-size: 26px;
    resize: none;
  }

  .rotateInUpRight{
    animation-delay: 2500ms;
  }

</style>



