<template>
  <div id="translateform">
  	<div id="formDiv">
  	<!--v-on 指令用于监听DOM事件-->
    <form id="transForm" v-on:submit="formSubmit" class="row">
    	<!--v-model用在表单控件上的，用于实现双向数据绑定-->
    	<!--<textarea class="form-control" type="text" placeholder="输入要翻译的内容" v-model="textToTranslate"/>-->
      <div class="text col-md-6 col-md-offset-2 animated rotateInDownLeft">
      <textarea  placeholder="输入要翻译的内容" v-model="textToTranslate"  cols="60" rows="10"></textarea>
      </div>
      <div class="select col-md-1 col-md-offset-1 animated rotateIn ">
    	<select id="select" v-model="language">
    		  <option value="en">英语</option>
          <option value="zh">中文</option>
          <option value="ru">俄语</option>
          <option value="ko">韩语</option>
          <option value="ja">日语</option>
          <option value="pl">波兰语</option>
          <option value="pt">葡萄牙语</option>
          <option value="it">意大利语</option>
          <option value="fr">法语</option>
      </select>
    	<input class="btn btn-info" type="submit" value="翻译"/>
        </div>
    </form>
    	</div>
  </div>
</template>

<script>


export default {
  name: 'translateform',
  data(){
  	return{
  		textToTranslate:"",
  		language:""
  	}
  },
  components: {

  },
  methods:{

  	//点击翻译时，将内容传递给app.vue
  	formSubmit:function(event){

  		//绑定数据传递事件
  		this.$emit("formSubmit",this.textToTranslate,this.language);
  		//阻止默认事件
  		event.preventDefault();
  	}

  },
  created:function(){
  	this.language="en";
  }
}
</script>

<style>
 /*#transForm{
    border-radius: 10px;
    border: 1px #ccc solid;
  }*/

 #translateform{
   padding: 0;
 }
  #formDiv{
  	margin: 120px auto;
  }

  .select{
    display: flex;
    flex-direction: column;
    width: 150px;
    margin-top: 40px;
  }
  #select{
    height: 40px;
    font-size: 18px;
    margin-bottom: 30px;
  }
 textarea{
   max-width: 463px;
   max-height: 262px;
   margin: 0 auto;
   border-radius: 15px;
   padding: 10px;
   font-size: 26px;
   resize: none;
 }
  .rotateInDownLeft{
    animation-delay: 2000ms;
  }

  .rotateIn{
    animation-delay: 3000ms;
  }
</style>



